import React, { useState, useCallback } from "react";
import styled from "styled-components";

const StyledButton = styled.button`
  background: rebeccapurple;
  border: none;
  color: white;
  padding: 4px 10px;
`;

const Wave = () => {
  const [waves, setWaves] = useState(0);
  const label = `${waves} 次挥手`;
  const increaseWaves = useCallback(() => {
    setWaves(waves => waves + 1);
  }, [setWaves]);

  return <StyledButton onClick={increaseWaves}>{label}</StyledButton>;
};

export default Wave;
